import { FC } from 'react';

import { AuthorHighlightProps } from '../AuthorHighlight.types';
import Authors from '../Authors';

import {
  Wrapper,
  Text,
  TextContent,
} from './AuthorHighlightTravelPrimary.styles';
import AuthorPrimary from './AuthorPrimary';

const AuthorHighlightTravelPrimary: FC<AuthorHighlightProps> = ({
  biography,
  authors,
}) => {
  if (authors?.length === 1) {
    return <AuthorPrimary biography={biography} author={authors[0]} />;
  }

  return (
    <Wrapper>
      <Text>
        {biography && <TextContent>{`${biography} `}</TextContent>}
        <Authors authors={authors} />
      </Text>
    </Wrapper>
  );
};

export default AuthorHighlightTravelPrimary;
