import { FC } from 'react';

import Author from './Author';
import { Wrapper } from './AuthorHighlight.styles';
import { AuthorHighlightProps } from './AuthorHighlight.types';
import Authors from './Authors';
import Card from './Card';

const AuthorHighlight: FC<AuthorHighlightProps> = ({
  biography,
  authors,
  image,
  className,
}) => {
  if (authors?.length === 1) {
    return (
      <Wrapper className={className}>
        <Author biography={biography} author={authors[0]} image={image} />
      </Wrapper>
    );
  }

  return (
    <Wrapper className={className}>
      <Card biography={biography}>
        <Authors authors={authors} />
      </Card>
    </Wrapper>
  );
};

export default AuthorHighlight;
