import { FC } from 'react';

import {
  CardWrapper,
  CardImage,
  CardText,
  CardTextContent,
} from './AuthorHighlight.styles';
import { CardProps } from './AuthorHighlight.types';

const Card: FC<CardProps> = ({ biography, image, children }) => (
  <CardWrapper>
    {image && <CardImage>{image}</CardImage>}
    <CardText>
      {children}
      {biography && <CardTextContent>{biography}</CardTextContent>}
    </CardText>
  </CardWrapper>
);

export default Card;
