import { FC } from 'react';
import { styled } from 'styled-components';

import { AuthorProps } from './AuthorHighlight.types';
import Card from './Card';

export const CardTextAuthor = styled.strong``;

const Author: FC<AuthorProps> = ({
  biography,
  author: { name, path },
  image,
}) => (
  <a href={path}>
    <Card biography={biography} image={image}>
      <CardTextAuthor>{name}</CardTextAuthor>
    </Card>
  </a>
);

export default Author;
